.leftpbox {
	// width: 200px;
	// height: 100vh;
	display: grid;
	grid-template-columns: 50px 50px;
	background-color: orange;
	column-gap: 10px;
	row-gap: 10px;

	// align-items: center;
	background-color: white;
	padding: 15px;
	font-size: 1.5rem;
	user-select: none;
}

.sqBtn {
	// width: 50px;
	height: 50px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	box-shadow: inset 0px 0px 5px -1px rgba(128, 128, 128, 1);
	border-radius: 5px;
	user-select: none;
}

.sqtow {
	grid-column: 1 / 3;
}

.active {
	border: 2px solid rgb(189, 189, 189);
	background-color: rgb(219, 219, 219) !important;
}

.colActive {
	outline: 3px dotted white;
	outline-offset: -3px;
	border: 3px solid black;
}

.sqBtn:hover {
	cursor: pointer;
	background-color: rgb(247, 247, 247);
}

.disable {
	opacity: 0.3;
	pointer-events: none;
}

.disable:hover {
	cursor: auto;
}

